import React, { useState, useEffect } from 'react';
import Header from './Header';
import TodoSection from './TodoSection';
import AnalyzerSection from './AnalyzerSection';

const App = () => {
  const [tasks, setTasks] = useState(() => {
    const savedTasks = localStorage.getItem('tasks');
    return savedTasks ? JSON.parse(savedTasks) : [];
  });
  const [currentView, setCurrentView] = useState('daily');
  const [currentFilter, setCurrentFilter] = useState('all');

  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
  }, [tasks]);

  const addTask = (text, date, priority) => {
    const newTask = {
      id: Date.now(),
      text,
      date,
      priority,
      completed: false,
      createdAt: new Date().toISOString()
    };
    setTasks([...tasks, newTask]);
  };

  const toggleTask = (id) => {
    setTasks(tasks.map(task => 
      task.id === id ? { ...task, completed: !task.completed } : task
    ));
  };

  const deleteTask = (id) => {
    setTasks(tasks.filter(task => task.id !== id));
  };

  return (
    <div className="container">
      <Header />
      
      <div className="view-toggle">
        <button 
          className={`view-btn ${currentView === 'daily' ? 'active' : ''}`} 
          onClick={() => setCurrentView('daily')}>
          Daily View
        </button>
        <button 
          className={`view-btn ${currentView === 'weekly' ? 'active' : ''}`} 
          onClick={() => setCurrentView('weekly')}>
          Weekly View
        </button>
        <button 
          className={`view-btn ${currentView === 'matrix' ? 'active' : ''}`} 
          onClick={() => setCurrentView('matrix')}>
          Priority Matrix
        </button>
      </div>

      <div className="main-content">
        <TodoSection 
          tasks={tasks}
          addTask={addTask}
          toggleTask={toggleTask}
          deleteTask={deleteTask}
          currentView={currentView}
          currentFilter={currentFilter}
          setCurrentFilter={setCurrentFilter}
        />
        <AnalyzerSection tasks={tasks} />
      </div>
    </div>
  );
};

export default App; 