import React, { useState, useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

const AnalyzerSection = ({ tasks }) => {
  const [activeTab, setActiveTab] = useState('daily');
  const dailyChartRef = useRef(null);
  const weeklyChartRef = useRef(null);
  const dailyPieChartRef = useRef(null);
  const weeklyPieChartRef = useRef(null);
  
  const chartRefs = useRef({
    dailyChart: null,
    weeklyChart: null,
    dailyPieChart: null,
    weeklyPieChart: null
  });

  // Calculate stats for display
  const totalTasks = tasks.length;
  const completedTasks = tasks.filter(task => task.completed).length;
  const completionRate = totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0;

  // Get today's tasks for daily stats
  const today = new Date().toISOString().split('T')[0];
  const todayTasks = tasks.filter(task => task.date === today);
  const todayCompleted = todayTasks.filter(task => task.completed).length;
  const todayProgress = todayTasks.length > 0 ? (todayCompleted / todayTasks.length) * 100 : 0;

  // Helper function to create or update a chart
  const createOrUpdateChart = (canvasRef, chartType, chartRef, data, options) => {
    try {
      if (!canvasRef.current) return;
      
      // Destroy existing chart instance if it exists
      if (chartRefs.current[chartRef]) {
        chartRefs.current[chartRef].destroy();
      }
      
      // Create new chart
      const ctx = canvasRef.current.getContext('2d');
      if (ctx) {
        chartRefs.current[chartRef] = new Chart(ctx, {
          type: chartType,
          data: data,
          options: options || {
            responsive: true,
            maintainAspectRatio: false
          }
        });
      }
    } catch (error) {
      console.error(`Error creating ${chartRef}:`, error);
    }
  };

  // Initialize charts
  useEffect(() => {
    // Helper function to get dates for the last 7 days
    const getLast7Days = () => {
      const dates = [];
      for (let i = 6; i >= 0; i--) {
        const date = new Date();
        date.setDate(date.getDate() - i);
        dates.push(date.toISOString().split('T')[0]);
      }
      return dates;
    };

    const dates = getLast7Days();
    const completedCounts = dates.map(date => 
      tasks.filter(task => task.date === date && task.completed).length
    );

    // Daily Chart Data
    const dailyChartData = {
      labels: dates.map(date => new Date(date).toLocaleDateString('en-US', { weekday: 'short' })),
      datasets: [{
        label: 'Tasks Completed',
        data: completedCounts,
        backgroundColor: '#3498db',
      }]
    };
    
    // Weekly Chart Data
    const weeks = [];
    for (let i = 3; i >= 0; i--) {
      const date = new Date();
      date.setDate(date.getDate() - (i * 7));
      const weekStart = new Date(date);
      weekStart.setDate(date.getDate() - date.getDay());
      weeks.push(weekStart);
    }
    
    const weeklyData = weeks.map(weekStart => {
      const weekEnd = new Date(weekStart);
      weekEnd.setDate(weekStart.getDate() + 6);
      
      return tasks.filter(task => {
        const taskDate = new Date(task.date);
        return taskDate >= weekStart && taskDate <= weekEnd && task.completed;
      }).length;
    });
    
    const weeklyChartData = {
      labels: weeks.map(date => {
        const endDate = new Date(date);
        endDate.setDate(date.getDate() + 6);
        return `${date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })} - ${endDate.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}`;
      }),
      datasets: [{
        label: 'Tasks Completed',
        data: weeklyData,
        borderColor: '#3498db',
        tension: 0.1
      }]
    };
    
    // Daily Pie Chart Data
    const dailyPieData = {
      labels: ['Completed', 'Active'],
      datasets: [{
        data: [todayCompleted, todayTasks.length - todayCompleted],
        backgroundColor: ['#27ae60', '#e74c3c'],
      }]
    };
    
    // Weekly Pie Chart Data
    const dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const dayTaskCounts = Array(7).fill(0);
    
    tasks.forEach(task => {
      if (task.completed) {
        const taskDate = new Date(task.date);
        const dayOfWeek = taskDate.getDay();
        dayTaskCounts[dayOfWeek]++;
      }
    });
    
    const weeklyPieData = {
      labels: dayNames,
      datasets: [{
        data: dayTaskCounts,
        backgroundColor: [
          '#1abc9c', '#2ecc71', '#3498db', 
          '#9b59b6', '#e74c3c', '#f1c40f', '#e67e22'
        ],
      }]
    };
    
    const chartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        y: {
          beginAtZero: true,
          ticks: { stepSize: 1 }
        }
      }
    };
    
    const pieChartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        legend: { position: 'bottom' },
        tooltip: {
          callbacks: {
            label: function(context) {
              let label = context.label || '';
              let value = context.raw || 0;
              let total = context.dataset.data.reduce((a, b) => a + b, 0);
              let percentage = total > 0 ? Math.round((value / total) * 100) : 0;
              return `${label}: ${value} (${percentage}%)`;
            }
          }
        }
      }
    };
    
    // Create/update all charts
    createOrUpdateChart(dailyChartRef, 'bar', 'dailyChart', dailyChartData, chartOptions);
    createOrUpdateChart(weeklyChartRef, 'line', 'weeklyChart', weeklyChartData, chartOptions);
    createOrUpdateChart(dailyPieChartRef, 'pie', 'dailyPieChart', dailyPieData, pieChartOptions);
    createOrUpdateChart(weeklyPieChartRef, 'pie', 'weeklyPieChart', weeklyPieData, pieChartOptions);
    
    // Cleanup function to destroy charts when component unmounts
    return () => {
      Object.values(chartRefs.current).forEach(chart => {
        if (chart) chart.destroy();
      });
    };
  }, [tasks, todayCompleted, todayTasks.length]);

  // Find the most productive day
  const findMostProductiveDay = () => {
    const dayTaskCounts = Array(7).fill(0);
    
    tasks.forEach(task => {
      if (task.completed) {
        const taskDate = new Date(task.date);
        const dayOfWeek = taskDate.getDay();
        dayTaskCounts[dayOfWeek]++;
      }
    });
    
    const maxTaskCount = Math.max(...dayTaskCounts);
    if (maxTaskCount === 0) return '-';
    
    const dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const mostProductiveIndex = dayTaskCounts.indexOf(maxTaskCount);
    return dayNames[mostProductiveIndex];
  };

  return (
    <div className="analyzer-section">
      <h2>TASK ANALYZER</h2>
      <div className="stats">
        <div className="stat-card">
          <h3>Total Tasks</h3>
          <p>{totalTasks}</p>
        </div>
        <div className="stat-card">
          <h3>Completed</h3>
          <p>{completedTasks}</p>
        </div>
        <div className="stat-card">
          <h3>Completion Rate</h3>
          <p>{completionRate}%</p>
        </div>
      </div>
      <div className="analyzer-tabs">
        <button 
          className={`analyzer-tab ${activeTab === 'daily' ? 'active' : ''}`} 
          onClick={() => setActiveTab('daily')}
        >
          Daily Analysis
        </button>
        <button 
          className={`analyzer-tab ${activeTab === 'weekly' ? 'active' : ''}`} 
          onClick={() => setActiveTab('weekly')}
        >
          Weekly Analysis
        </button>
      </div>
      <div className="analyzer-content">
        <div id="dailyAnalysis" className={`analysis-view ${activeTab === 'daily' ? 'active' : ''}`}>
          <div className="chart-container">
            <div className="bar-chart">
              <h3>Task Completion Trend</h3>
              <div className="productivity-chart">
                <canvas ref={dailyChartRef}></canvas>
              </div>
            </div>
            <div className="pie-chart">
              <h3>Daily Task Distribution</h3>
              <div className="distribution-chart">
                <canvas ref={dailyPieChartRef}></canvas>
              </div>
            </div>
          </div>
          <div className="daily-stats">
            <h3>Today's Progress</h3>
            <div className="progress-bar">
              <div 
                id="todayProgress" 
                className="progress" 
                style={{ width: `${todayProgress}%` }}
              ></div>
            </div>
            <p>{todayCompleted}/{todayTasks.length} tasks completed</p>
          </div>
        </div>
        <div id="weeklyAnalysis" className={`analysis-view ${activeTab === 'weekly' ? 'active' : ''}`}>
          <div className="chart-container">
            <div className="line-chart">
              <h3>Weekly Completion Trend</h3>
              <div className="productivity-chart">
                <canvas ref={weeklyChartRef}></canvas>
              </div>
            </div>
            <div className="pie-chart">
              <h3>Weekly Task Distribution</h3>
              <div className="distribution-chart">
                <canvas ref={weeklyPieChartRef}></canvas>
              </div>
            </div>
          </div>
          <div className="weekly-stats">
            <h3>Weekly Overview</h3>
            <div className="week-stats-grid">
              <div className="week-stat">
                <h4>Most Productive Day</h4>
                <p>{findMostProductiveDay()}</p>
              </div>
              <div className="week-stat">
                <h4>Weekly Goal Progress</h4>
                <p>{completionRate}%</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AnalyzerSection; 