import React from 'react';
import TaskItem from './TaskItem';

const DailyView = ({ tasks, toggleTask, deleteTask, currentFilter }) => {
  // Get today's date in YYYY-MM-DD format for comparison
  const today = new Date().toISOString().split('T')[0];
  
  // Filter tasks based on the currentFilter
  const filteredTasks = tasks.filter(task => {
    if (currentFilter === 'all') return true;
    if (currentFilter === 'active') return !task.completed;
    if (currentFilter === 'completed') return task.completed;
    return true;
  });

  // Count today's tasks
  const todayTasks = tasks.filter(task => task.date === today);
  const todayCompleted = todayTasks.filter(task => task.completed);
  const todayActive = todayTasks.filter(task => !task.completed);

  return (
    <div id="dailyView" className="task-view active">
      <div className="daily-summary">
        <div className="summary-item">
          <span className="summary-label">Today's Tasks:</span>
          <span className="summary-value">{todayTasks.length}</span>
        </div>
        <div className="summary-item">
          <span className="summary-label">Completed:</span>
          <span className="summary-value success">{todayCompleted.length}</span>
        </div>
        <div className="summary-item">
          <span className="summary-label">Active:</span>
          <span className="summary-value warning">{todayActive.length}</span>
        </div>
      </div>
      <ul id="taskList">
        {filteredTasks.map(task => (
          <TaskItem
            key={task.id}
            task={task}
            toggleTask={toggleTask}
            deleteTask={deleteTask}
          />
        ))}
      </ul>
    </div>
  );
};

export default DailyView; 