import React, { useState, useEffect } from 'react';
import TaskItem from './TaskItem';

const WeeklyView = ({ tasks, toggleTask, deleteTask }) => {
  const [currentWeek, setCurrentWeek] = useState(new Date());
  const [weekRange, setWeekRange] = useState('');
  
  // Function to get week dates (Sunday to Saturday)
  const getWeekDates = (date) => {
    const day = date.getDay(); // 0 for Sunday, 1 for Monday, etc.
    const diff = date.getDate() - day;
    const sunday = new Date(date);
    sunday.setDate(diff);
    
    const weekDates = [];
    for (let i = 0; i < 7; i++) {
      const nextDate = new Date(sunday);
      nextDate.setDate(sunday.getDate() + i);
      weekDates.push(nextDate);
    }
    return weekDates;
  };
  
  // Update week display whenever currentWeek changes
  useEffect(() => {
    const weekDates = getWeekDates(currentWeek);
    const startDate = weekDates[0].toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
    const endDate = weekDates[6].toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
    setWeekRange(`Week of ${startDate} - ${endDate}`);
  }, [currentWeek]);
  
  // Navigate to previous week
  const goToPrevWeek = () => {
    const prevWeek = new Date(currentWeek);
    prevWeek.setDate(prevWeek.getDate() - 7);
    setCurrentWeek(prevWeek);
  };
  
  // Navigate to next week
  const goToNextWeek = () => {
    const nextWeek = new Date(currentWeek);
    nextWeek.setDate(nextWeek.getDate() + 7);
    setCurrentWeek(nextWeek);
  };
  
  // Generate the week grid with tasks
  const weekDates = getWeekDates(currentWeek);
  
  // Function to format date to YYYY-MM-DD for comparison
  const formatDateForComparison = (date) => {
    return date.toISOString().split('T')[0];
  };
  
  return (
    <div id="weeklyView" className="task-view">
      <div className="week-container">
        <div className="week-header">
          <button id="prevWeek" onClick={goToPrevWeek}>
            <i className="fas fa-chevron-left"></i>
          </button>
          <h3 id="weekRange">{weekRange}</h3>
          <button id="nextWeek" onClick={goToNextWeek}>
            <i className="fas fa-chevron-right"></i>
          </button>
        </div>
        <div className="week-grid">
          {weekDates.map((date, index) => {
            const formattedDate = formatDateForComparison(date);
            const dayTasks = tasks.filter(task => task.date === formattedDate);
            const dayName = date.toLocaleDateString('en-US', { weekday: 'long' });
            
            return (
              <div key={index} className="day-column" data-day={index}>
                <h4>{dayName}</h4>
                <ul className="day-tasks">
                  {dayTasks.map(task => (
                    <TaskItem
                      key={task.id}
                      task={task}
                      toggleTask={toggleTask}
                      deleteTask={deleteTask}
                    />
                  ))}
                </ul>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default WeeklyView; 